.balloon{width:100%;height:100%;top:0;left:0;position:fixed;z-index:99999;background-color:rgba(0,0,0,0.6)}.balloon>div{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;height:100%}.balloon .container{width:286px;background-color:#fff;border-radius:4px;margin:0 auto;z-index:99;-webkit-animation:fadeIn 1s ease both}.balloon .balloon-head{width:100%;height:45px;padding:0 24px;line-height:44px;font-size:16px;color:#666;border-bottom:1px solid #d9dfe4;border-radius:4px 4px 0 0;background-color:#f5f5f5}.balloon .balloon-head>span{display:block;float:left;height:44px;line-height:44px}.balloon .balloon-head:after{content:"";display:block;clear:both}.balloon .balloon-body{color:#ff6600;max-height:360px;text-align:center;line-height:30px;padding:15px 20px}.balloon .balloon-foot{display:-webkit-box;padding:0 10px 20px 10px;border-radius:0 0 2px 2px}.balloon .balloon-foot button{display:block;border:0;outline:0;height:34px;margin:0 10px;font-size:15px;line-height:32px;text-align:center;border-radius:3px;-webkit-box-flex:1}.balloon .balloon-foot button.btn-primary{color:#fff;background-color:#028dea;border:1px solid #1386d4}.balloon .balloon-foot button.btn-danger{color:#fff;margin-right:10px;background-color:#FF4C4C;border:1px solid #dc1b1b}.balloon .balloon-foot button.btn-default{color:#666;margin-left:10px;background-color:#f2f2f2;border:1px solid #ddd}



