.statu-tips{position:fixed;width:100%;top:46px;height:0px;line-height:30px;background-color:#f5f5f5;font-size:15px;color:#999;padding:0 10px;z-index:999;white-space:nomal;overflow:hidden;text-overflow:ellipsis;-webkit-transition:height 0.3s ease-in;transition:height 0.3s ease-in}.statu-tips.down{height:31px}.statu-tips i{display:inline-block;vertical-align:top;height:20px;width:20px;border-radius:50%;border:1px solid #ff6600;position:relative;margin-right:5px;box-sizing:border-box;margin-top:5px}.statu-tips i:before{position:absolute;display:block;content:"";height:8px;width:2px;left:8px;top:3px;background-color:#ff6600}.statu-tips i:after{position:absolute;display:block;content:"";width:2px;height:2px;background-color:#ff6600;top:13px;left:8px}



